
@import url('https://fonts.googleapis.com/css2?family=BIZ+UDMincho&family=Lato:ital,wght@1,300&family=Oswald:wght@200&family=Satisfy&family=Source+Sans+Pro:wght@200&display=swap');

:root {
	--bg-1: #fafafa;
  --bg-2: #ffffff;
  --border: #dbdbdb;
}

* {
	box-sizing: border-box;
	font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;

}

html {
	display: flex;
	min-height: 100%;
}

body {
	background-color: var(--bg-1);
	color: var(--our-white);
	font-size: 18px;
	flex: 1;
	display: flex;
	flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  align-items: center;
 
}

#root {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding-top: 54px;
}

.posts {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: center;
  
}

.post {
  background-color: var(--bg-2);
	display: flex;
  flex-direction: column;
  border: 1px solid var(--border);
  width: 100%;
  max-width: 468px;
  border-radius: 8px;
  
}

.post .header{
  padding: 8px 12px;
}


.post .header {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
}

.post .header img {
  width: 32px;
  height: 32px;
  border: 1px solid rgb(251, 35, 118);
  border-radius: 50%;
}

.post .header h2 {
  font-size: 14px;
}

.post .footer .icons {
  display: flex;
  flex-direction: row;
  padding: 4px 4px;
}

.post .footer .icons .icon {
  padding: 8px;
}

.post .footer .icons .icon.heart:hover path {
  fill: #ed4956;
  color: #ed4956;
}

.post .footer .description {
  font-size: 14px;
  padding: 8px 12px 24px;
}


.post img {
  width: 100%;
  max-height: 80vh;
  object-fit: cover;
}

.spacer {
  flex: 1;
}

/* nav */

.navBar {
	background-color: #ffffff;
	height: 80px;
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0px 50px;
	box-sizing: border-box;
	z-index: 2;
	/* animation magic */
	transition: all 0.4s ease-in-out;
	-webkit-transition: all 0.4s ease-in-out;
	-moz-transition: all 0.4s ease-in-out;
  }

  .navigation-search-container {
	position: relative;
  }

  .navigation-search-container  {
	background-color: #fafafa;
	padding: 3px 20px;
	padding-left: 25px;
	height: 30px;
	box-sizing: border-box;
	border: 1px solid rgba(0, 0, 0, 0.0975);
	border-radius: 3px;
	font-size: 14px;
  }
